import React, { useState } from 'react';
import styled from 'styled-components';
import Slider from "react-slick";
import SectionTitle from './section-titles';

const Card = styled.div`
  width: 275px;
  height: 485px;
  margin-left: 5px;
  margin-right: 5px;
  background: white;
  box-shadow: 0px 0px 30px rgba(51, 1, 109, 0.1);
  border-radius: 8px;
  border-bottom: 10px solid ${props => props.color};
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: center;
  background-color: ${props => props.bg};
`

const Reasons = ({ sectionTitle, reasonsData, textClassName, isDark }) => {
  const reasonSlides = reasonsData.map((r, index) => (
    <div key={index + r.title}>
      <CardContent
        title={r.title}
        content={r.content}
        image={r.image}
        color={r.color}
        bgColor={r.bgColor}
        textClassName={textClassName}
      />
    </div>
  ));

  return (
    <>
      <section id='reasons' className='spacing-bottom'>
        <div className='row mb-5'>
          <SectionTitle>
            {sectionTitle}
            <hr className={'center ' + (isDark && 'blue') } />
          </SectionTitle>
          <div className='row w-100 d-none d-xl-flex justify-content-center align-items center'>
            {reasonSlides}
          </div>
          <div className='col-12 px-md-5 d-block d-xl-none reasons-slider'>
            <Slider {...settings}>
              {reasonSlides}
            </Slider>
          </div>
        </div>
      </section>
    </>
  );
}

const CardContent = ({ title, image, content, color, bgColor, textClassName }) => {
  const [isOpen, setOpen] = useState(false);
  if (isOpen) {
    return (
      <Card className='px-3 pt-3 text-left card-open' bg={color} color={color} onMouseLeave={(_) => setOpen(false)}>
        <p className={textClassName}>{content}</p>
        <div className='text-center'>
          <span className='icon reason-close'></span>
        </div>
      </Card>
    )
  } else {
    return (
      <Card className='px-2 py-3 text-center' bg={bgColor} color={color} onMouseEnter={(_) => setOpen(true)}>
        <h3 className={textClassName}>{title}</h3>
        <img src={image} alt='Причина 1' />
        <div>
          <span className='icon reason-open'></span>
        </div>
      </Card>
    );
  }

}

const settings = {
  dots: false,
  speed: 1000,
  arrows: false,
  infinite: false,
  // centerMode: true,
  slidesToShow: 4,
  slidesToScroll: 2,
  focusOnSelect: true,
  pauseOnFocus: true,
  responsive: [
    {
      breakpoint: 1200,
      settings: {
        slidesToShow: 3,
        slidesToScroll: 3,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 930,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 690,
      settings: {
        slidesToShow: 2,
        slidesToScroll: 2,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 500,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 400,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
    {
      breakpoint: 370,
      settings: {
        slidesToShow: 1,
        centerMode: true,
        centerPadding: 0,
        arrows: false,
        dots: true,
      }
    },
  ],
}

