import React from 'react';
import Hero from '../components/shared/hero';
import Layout from "../components/layout"
import { Link, graphql } from 'gatsby';
import SEO from "../components/seo";
import styled from 'styled-components';
import ScrollAnimation from 'react-animate-on-scroll';

import '../styles/business.css';
import Reasons from '../components/shared/reasons';
import constants from '../constants/business';
import BottomCta from '../components/shared/bottom-cta';
import TestimonialSlider from '../components/shared/testimonial-slider';
import CallCTA from '../components/business/call-cta';
import VerticalSteps from '../components/shared/vertical-steps';
import CollapsibleSection from '../components/shared/collapsable';
import ClientLogosSection from '../components/business/client-logos-section';

import heroImage from './../images/business/heroImg.svg';
import leftImageSteps from '../images/business/vertical-steps.svg'

const BusinessPage = ({ data }) => {
  return (
    <>
      <div className='business-page'>
        <BackgroundWrapper
          bg={data.bgImage.childImageSharp.fluid.src}
        >
          <Layout
            activeTab={'/za-biznesa'}
          >
            <SEO title='За Бизнеса' />

            <Hero
              heroImage={heroImage}
              title={(<h1 className='text-white mt-3'>Твоят бизнес си е твоя работа,<br />нашият е да го правим лека работа</h1>)}
            />

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <ClientLogosSection
                logos={data.logos}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <Reasons
                sectionTitle={<h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>Четири причини офисът<br /> ти да е на вълната <br />на NetSurf</h2>}
                reasonsData={constants.reasons}
                textClassName={'text-white'}
                isDark={true}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >

              <CallCTA
                bgImg={data.ctaBg.childImageSharp.fluid.src}
                bgPerson={data.ctaPerson.childImageSharp.fluid}
              >
                <div className='text-md-left text-center'>
                  <h2 className='mb-md-5 mb-2 d-nones d-md-blocks text-white'>Всичко е лично,<br /> особено в бизнеса</h2>
                  <p className='mb-md-5 mb-3 text-white'>Рамо до рамо ще изготвим индивидуален пакет за твоя бизнес.</p>
                  <div className='d-inline-block'>
                    <a href={'tel:070013888'} className='btn btn-primary green text-left d-inline-block mt-2 mr-md-2 mb-2'>Обади ни се 0700 13 888</a>
                  </div>
                  <div className='d-inline-block'>
                    <button className='btn btn-primary dark-blue'>Изпрати запитване</button>
                  </div>
                </div>
              </CallCTA>
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <VerticalSteps
                title={<h2 className='text-white'>Трите крачки <br />към летящия ни <br />старт заедно</h2>}
                isDark={true}
                steps={constants.verticalSteps}
                leftImage={leftImageSteps}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <TestimonialSlider
                sectionTitle={<h2 className='text-white'>Какво мислят<br />другите за нас</h2>}
                opinions={constants.clientOpinions}
                isWhiteArrows={true}
                isDark={true}
              />
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <BottomCta
                bgImg={data.ctaBg.childImageSharp.fluid.src}
              >
                <h2 className='mb-md-5 mb-2 d-none d-md-block text-white'>Нямаме търпение да ти покажем<br /> колко сме добри!</h2>
                <h2 className='mb-md-5 mb-2 d-block d-md-none text-white'>Нямаме търпение да ти покажем колко сме добри!</h2>
                <p className='mb-md-5 mb-3 text-white'>Нашите предложения са ти точно по мярка</p>
                <Link to={'/paketi'} className='btn btn-primary green'>Нека го обсъдим</Link>
              </BottomCta>
            </ScrollAnimation>

            <ScrollAnimation
              animateIn='fadeIn'
              animateOnce={true}
            >
              <CollapsibleSection
                isDark={true}
                faqs={constants.faqs}
                sectionTitle={(
                  <>
                    <h2 className='text-white' style={{ whiteSpace: 'pre-wrap' }}>Често задавани въпроси</h2>
                    <hr className='center blue' />
                  </>
                )}
              />
            </ScrollAnimation>
          </Layout>
        </BackgroundWrapper>
      </div>
    </>
  )
}

const BackgroundWrapper = styled.div`
  background-image: url(${props => props.bg});
      background-position: top;
      background-size: cover;
      background-repeat: repeat-y;
`

export const query = graphql`
  query BusinessPageQuery {
    bgImage: file(relativePath: {eq: "business/business-service-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 6056) {
          src
        }
      }
    }
    ctaBg: file(relativePath: {eq: "business/cta-bg.png"}) {
      childImageSharp {
        fluid(maxHeight: 838) {
          src
        }
      }
    }
    ctaPerson: file(relativePath: {eq: "business/call-cta-person.png"}) {
      childImageSharp {
        fluid(maxHeight: 1038) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    logos: allFile(filter: {sourceInstanceName: {eq: "logo-clients-images"}}) {
      edges {
        node {
          childImageSharp {
            fluid(maxWidth: 360) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
  }`


export default BusinessPage;
